<template><section>

<h1>Card 卡片</h1>
<p>基本容器，用来进行文字、列表、图文等展示</p>
<h2 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-card title=\&quot;列表展示\&quot;&gt;\n  &lt;p&gt;item1&lt;/p&gt;\n  &lt;p&gt;item2&lt;/p&gt;\n  &lt;p&gt;item3&lt;/p&gt;\n&lt;/xl-card&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-card title="列表展示">
  <p>item1</p>
  <p>item2</p>
  <p>item3</p>
</xl-card>
</div>
                            <div slot="desc"><p>使用<code>title</code>指定标题</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"列表展示"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item1<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item2<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item3<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="wu-bian-kuang"><a class="header-anchor" href="#wu-bian-kuang" aria-hidden="true">¶</a> 无边框</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-card__border\&quot;&gt;\n  &lt;xl-card title=\&quot;列表展示\&quot; :border=\&quot;false\&quot;&gt;\n    &lt;p&gt;item1&lt;/p&gt;\n    &lt;p&gt;item2&lt;/p&gt;\n    &lt;p&gt;item3&lt;/p&gt;\n  &lt;/xl-card&gt;\n&lt;/div&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n.demo-card__border {\n  padding: 2em;\n  background-color: #eee;\n}\n&quot;}">
                            <div><div class="demo-card__border">
  <xl-card title="列表展示" :border="false">
    <p>item1</p>
    <p>item2</p>
    <p>item3</p>
  </xl-card>
</div>

</div>
                            <div slot="desc"><p>使用<code>border</code>指定是否显示边框</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-card__border"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"列表展示"</span> <span class="hljs-attr">:border</span>=<span class="hljs-string">"false"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item1<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item2<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item3<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
<span class="hljs-selector-class">.demo-card__border</span> {
  <span class="hljs-attribute">padding</span>: <span class="hljs-number">2em</span>;
  <span class="hljs-attribute">background-color</span>: <span class="hljs-number">#eee</span>;
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">style</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="xian-shi-yin-ying"><a class="header-anchor" href="#xian-shi-yin-ying" aria-hidden="true">¶</a> 显示阴影</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-row :gutter=\&quot;20\&quot;&gt;\n  &lt;xl-col :span=\&quot;8\&quot;&gt;\n    &lt;xl-card title=\&quot;一直不显示阴影\&quot; shadow=\&quot;never\&quot;&gt;\n      &lt;p&gt;item1&lt;/p&gt;\n      &lt;p&gt;item2&lt;/p&gt;\n      &lt;p&gt;item3&lt;/p&gt;\n    &lt;/xl-card&gt;\n  &lt;/xl-col&gt;\n  &lt;xl-col :span=\&quot;8\&quot;&gt;\n    &lt;xl-card title=\&quot;一直显示阴影\&quot; shadow=\&quot;always\&quot;&gt;\n      &lt;p&gt;item1&lt;/p&gt;\n      &lt;p&gt;item2&lt;/p&gt;\n      &lt;p&gt;item3&lt;/p&gt;\n    &lt;/xl-card&gt;\n  &lt;/xl-col&gt;\n  &lt;xl-col :span=\&quot;8\&quot;&gt;\n    &lt;xl-card title=\&quot;鼠标悬停显示阴影\&quot; shadow=\&quot;hover\&quot;&gt;\n      &lt;p&gt;item1&lt;/p&gt;\n      &lt;p&gt;item2&lt;/p&gt;\n      &lt;p&gt;item3&lt;/p&gt;\n    &lt;/xl-card&gt;\n  &lt;/xl-col&gt;\n&lt;/xl-row&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                            <div><xl-row :gutter="20">
  <xl-col :span="8">
    <xl-card title="一直不显示阴影" shadow="never">
      <p>item1</p>
      <p>item2</p>
      <p>item3</p>
    </xl-card>
  </xl-col>
  <xl-col :span="8">
    <xl-card title="一直显示阴影" shadow="always">
      <p>item1</p>
      <p>item2</p>
      <p>item3</p>
    </xl-card>
  </xl-col>
  <xl-col :span="8">
    <xl-card title="鼠标悬停显示阴影" shadow="hover">
      <p>item1</p>
      <p>item2</p>
      <p>item3</p>
    </xl-card>
  </xl-col>
</xl-row>
</div>
                            <div slot="desc"><p>使用<code>shadow</code>指定是否显示阴影</p>
</div>
