  <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"8"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"一直不显示阴影"</span> <span class="hljs-attr">shadow</span>=<span class="hljs-string">"never"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item1<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item2<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item3<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"8"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"一直显示阴影"</span> <span class="hljs-attr">shadow</span>=<span class="hljs-string">"always"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item1<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item2<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item3<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">xl-col</span> <span class="hljs-attr">:span</span>=<span class="hljs-string">"8"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"鼠标悬停显示阴影"</span> <span class="hljs-attr">shadow</span>=<span class="hljs-string">"hover"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item1<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item2<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>item3<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">xl-col</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-row</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="ding-zhi-biao-ti-he-nei-rong"><a class="header-anchor" href="#ding-zhi-biao-ti-he-nei-rong" aria-hidden="true">¶</a> 定制标题和内容</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-card&gt;\n  &lt;p slot=\&quot;title\&quot; style=\&quot;margin:0;\&quot;&gt;\n    &lt;xl-icon type=\&quot;ios-film\&quot;&gt;&lt;/xl-icon&gt;\n    热门电影\n  &lt;/p&gt;\n  &lt;div slot=\&quot;extra\&quot;&gt;\n    &lt;xl-icon style=\&quot;padding-right:1em;cursor:pointer;color:#409eff;\&quot; type=\&quot;plus\&quot; @on-click=\&quot;plusClick\&quot;&gt;&lt;/xl-icon&gt;\n    &lt;xl-icon style=\&quot;cursor:pointer;color:#f56c6c;\&quot; type=\&quot;trash-b\&quot; @on-click=\&quot;trashClick\&quot;&gt;&lt;/xl-icon&gt;\n  &lt;/div&gt;\n  &lt;p&gt;\n    &lt;a href=\&quot;#\&quot;&gt;\n    一出好戏\n    &lt;/a&gt;\n  &lt;/p&gt;\n  &lt;p&gt;\n    &lt;a href=\&quot;#\&quot;&gt;\n    西红柿首富\n    &lt;/a&gt;\n  &lt;/p&gt;\n  &lt;p&gt;\n    &lt;a href=\&quot;#\&quot;&gt;\n    摩天救援\n    &lt;/a&gt;\n  &lt;/p&gt;\n&lt;/xl-card&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    plusClick() {\n      alert(\&quot;添加成功\&quot;)\n    },\n    trashClick() {\n      alert(\&quot;删除成功\&quot;)\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div><xl-card>
  <p slot="title" style="margin:0;">
    <xl-icon type="ios-film"></xl-icon>
    热门电影
  </p>
  <div slot="extra">
    <xl-icon style="padding-right:1em;cursor:pointer;color:#409eff;" type="plus" @on-click="plusClick"></xl-icon>
    <xl-icon style="cursor:pointer;color:#f56c6c;" type="trash-b" @on-click="trashClick"></xl-icon>
  </div>
  <p>
    <a href="#">
    一出好戏
    </a>
  </p>
  <p>
    <a href="#">
    西红柿首富
    </a>
  </p>
  <p>
    <a href="#">
    摩天救援
    </a>
  </p>
</xl-card>


</div>
                            <div slot="desc"><p>自定义插槽</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"title"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin:0;"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"ios-film"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
    热门电影
  <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"extra"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"padding-right:1em;cursor:pointer;color:#409eff;"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"plus"</span> <span class="hljs-attr">@on-click</span>=<span class="hljs-string">"plusClick"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-icon</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"cursor:pointer;color:#f56c6c;"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"trash-b"</span> <span class="hljs-attr">@on-click</span>=<span class="hljs-string">"trashClick"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-icon</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>
    一出好戏
    <span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>
    西红柿首富
    <span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>
    摩天救援
    <span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    plusClick() {
      alert(<span class="hljs-string">"添加成功"</span>)
    },
    trashClick() {
      alert(<span class="hljs-string">"删除成功"</span>)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="qia-pian-shi-bu-ju"><a class="header-anchor" href="#qia-pian-shi-bu-ju" aria-hidden="true">¶</a> 卡片式布局</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;xl-card style=\&quot;width:300px;overflow:hidden\&quot; :body-style=\&quot;{padding:0}\&quot;&gt;\n  &lt;img style=\&quot;width:300px;vertical-align:top;\&quot; src=\&quot;/static/card_1.png\&quot;&gt;\n  &lt;div slot=\&quot;footer\&quot; class=\&quot;demo-card__card\&quot;&gt;\n    &lt;xl-button icon=\&quot;gear-b\&quot; type=\&quot;text\&quot;&gt;&lt;/xl-button&gt;\n    &lt;xl-button icon=\&quot;edit\&quot; type=\&quot;text\&quot;&gt;&lt;/xl-button&gt;\n    &lt;xl-button icon=\&quot;more\&quot; type=\&quot;text\&quot;&gt;&lt;/xl-button&gt;\n  &lt;/div&gt;\n&lt;/xl-card&gt;\n\n&quot;,&quot;script&quot;:null,&quot;style&quot;:&quot;\n  .demo-card__card{\n    display: flex;\n    justify-content: space-around;\n  }\n&quot;}">
                            <div><xl-card style="width:300px;overflow:hidden" :body-style="{padding:0}">
  <img style="width:300px;vertical-align:top;" src="/static/card_1.png">
  <div slot="footer" class="demo-card__card">
    <xl-button icon="gear-b" type="text"></xl-button>
    <xl-button icon="edit" type="text"></xl-button>
    <xl-button icon="more" type="text"></xl-button>
  </div>
</xl-card>

</div>
                            <div slot="desc"><p>使用<code>footer</code>插槽</p>
</div>
                            <div slot="source"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">xl-card</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:300px;overflow:hidden"</span> <span class="hljs-attr">:body-style</span>=<span class="hljs-string">"{padding:0}"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:300px;vertical-align:top;"</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"/static/card_1.png"</span>/&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-card__card"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"gear-b"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"edit"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">xl-button</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"more"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">xl-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">xl-card</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">style</span>&gt;</span><span class="css">
  <span class="hljs-selector-class">.demo-card__card</span>{
    <span class="hljs-attribute">display</span>: flex;
    <span class="hljs-attribute">justify-content</span>: space-around;
