</div></demo-block>
<h2 id="card-shu-xing"><a class="header-anchor" href="#card-shu-xing" aria-hidden="true">¶</a> card 属性</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题（如果使用了title插槽，则这个属性不生效）</td>
<td>string</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>border</td>
<td>是否显示边框</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>shadow</td>
<td>是否显示阴影</td>
<td>string</td>
<td>hover/always/never</td>
<td>never</td>
</tr>
<tr>
<td>headerStyle</td>
<td>定制头部的样式</td>
<td>object</td>
<td>—</td>
<td>{}</td>
</tr>
<tr>
<td>bodyStyle</td>
<td>定制body的样式</td>
<td>object</td>
<td>—</td>
<td>{}</td>
</tr>
<tr>
<td>footerStyle</td>
<td>定制footer的样式</td>
<td>object</td>
<td>—</td>
<td>{}</td>
</tr>
<tr>
<td>headerClass</td>
<td>头部添加样式</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>bodyClass</td>
<td>body添加样式</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
<tr>
<td>footerClass</td>
<td>footer添加样式</td>
<td>string</td>
<td>—</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2 id="poptip-slots"><a class="header-anchor" href="#poptip-slots" aria-hidden="true">¶</a> Poptip slots</h2>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>default</td>
<td>card显示的主题内容</td>
</tr>
<tr>
<td>title</td>
<td>定制标题</td>
</tr>
<tr>
<td>footer</td>
<td>定制底部</td>
</tr>
<tr>
<td>extra</td>
