</tr>
</tbody>
</table>
</section></template>
<style>
.demo-card__border {
  padding: 2em;
  background-color: #eee;
}

.demo-card__card{
    display: flex;
    justify-content: space-around;
  }
</style>
<script>
  export default {
    methods: {
      plusClick() {
        alert("添加成功")
      },
      trashClick() {
        alert("删除成功")
      }
    }
  }
</script>


